import { useState } from 'react';
import { useSelector } from "react-redux";
import { useNavigate, useParams } from 'react-router-dom';
import { toast } from 'react-toastify';
import { useError } from "../../hooks/useError";
import { updateCategoryAPI } from '../../service/category.service';

export function EditCategory() {
    const { showError } = useError()
    let navigate = useNavigate()

    let { id } = useParams();
    const { categories } = useSelector((state) => state.category)

    //tim cate tu list lay ra tu redux store 
    let [category, setCategory] = useState(categories.find(c => c.id === parseInt(id)))

    const save = async () => {
        let { code } = await updateCategoryAPI(category)

        if (code === 200) {
            toast("Thanh cong!!", { position: toast.POSITION.TOP_CENTER, type: 'success', theme: 'colored' })
            navigate("/dashboard/categories")
        }
        else
            showError(code)
    }

    const handleChange = (e) => setCategory({
        ...category,
        [e.target.name]: e.target.value
    })

    return (
        <div>
            <h2>Update Category</h2>
            <div>
                <label>Id</label>
                <input onChange={handleChange} name="id" value={category?.id} />
            </div>
            <div>
                <label>Name</label>
                <input onChange={handleChange} name="name" value={category?.name} />
            </div>
            <div>
                <button type='button' onClick={save}>Update</button>
            </div>
        </div>
    )
}