import { axiosInstance } from "./myaxios";

export const deleteCategoryAPI = async (id) => {
    var config = {
        method: 'DELETE',
        url: `/admin/category/delete?id=${id}`
    };

    return handleResponse(config)
}

export const findCategoriesAPI = async (search) => {
    var config = {
        method: 'post',
        url: '/category/search',
        data: search
    };
    return handleResponse(config)
}

export const getCategoryByIdAPI = async (id) => {
    var config = {
        method: 'get',
        url: `/category/${id}`
    };
    return handleResponse(config)
}


export const addCategoryAPI = async (category) => {
    var config = {
        method: 'post',
        url: '/admin/category/add',
        data: category
    };

    return handleResponse(config)
}

export const updateCategoryAPI = async (category) => {
    var config = {
        method: 'put',
        url: '/admin/category/update',
        data: category
    };

    return handleResponse(config)
}

const handleResponse = async (config) => {
    try {
        let response = await axiosInstance(config)

        let result = response.data;
        return { code: 200, result }
    } catch (error) {
        console.log(error);

        if (error.response) {
            return { code: error.response.status }
        } else if (error.request) {
            return { code: 408 }
        } else {
            return { code: 500 }
        }
    }
}