import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useError } from "../../hooks/useError";
import { addCategoryAPI } from "../../service/category.service";
import { toast } from 'react-toastify';

export default function NewCategory() {
    let navigate = useNavigate()
    const { showError } = useError()

    //ham hook
    let [category, setCategory] = useState({
        name: ""
    });

    const addNew = async () => {
        let { code } = await addCategoryAPI(category)

        if (code === 200) {
            toast("Thanh cong!!", { position: toast.POSITION.TOP_CENTER, type: 'success', theme: 'colored' })
            navigate("/dashboard/categories")
        }
        else
            showError(code)
    }

    const handleChangeName = (e) => setCategory({
        ...category,
        name: e.target.value
    })

    return (
        <div className='row border'>
            <h2 className="h1">Create Category</h2>
            <div className="mb-3">
                <label className="form-label">Name</label>
                <input className='form-control' onChange={handleChangeName} />
            </div >
            <div className="mb-3">
                <button className="btn btn-danger" type='button' onClick={addNew}>Create</button>
            </div>
        </div>
    )
}